export default {
	data() {
		return {
			banners: [],
			list: [],
			content: '',  //倒计时内容
			isChecked: false,  //是否勾选免责条款
			endTime: "2019-12-31 19:00",
			organizerPhone: '19983067289',
			venueName: '波力羽毛球馆',
			address: '四川省成都市武侯区新乐北街8号',
			lat: '30.610880',
			long: '104.040560',
			activityId: null,
			actDetail: null,
			clubId: null
		}
	},
	created() {
		this.banners = [
		 {
			 imgUrl: 'https://weiliicimg6.pstatp.com/weili/l/255348441617793179.webp',
			 webUrl: 'https://www.laihu.baogongxia.com'
		 },
		 {
			 imgUrl: 'https://weiliicimg1.pstatp.com/weili/l/262282047841894496.webp',
			 webUrl: 'https://uniapp.dcloud.io/'
		 },
		 {
			 imgUrl: 'https://weiliicimg1.pstatp.com/weili/l/458286230512861820.webp',
			 webUrl: 'https://youzan.github.io/vant-weapp/#/intro'
		 },
		 {
			 imgUrl: 'https://icweiliimg6.pstatp.com/weili/l/328163178242899976.webp',
			 webUrl: 'https://www.baidu.com'
		 }
		]
		this.list = [
			"http://f1.haiqq.com/allimg/3831982416/2817233822.jpg",
			"http://f1.haiqq.com/allimg/3831982416/2817233822.jpg",
			"http://f1.haiqq.com/allimg/3831982416/2817233822.jpg",
			"http://f1.haiqq.com/allimg/3831982416/2817233822.jpg",
			"http://f1.haiqq.com/allimg/3831982416/2817233822.jpg",
			"http://f1.haiqq.com/allimg/3831982416/2817233822.jpg",
		]
	},
	onLoad(options) {
		console.log(options)
		this.activityId = options.actId
		this.$http.get({
			url: '/v1/rest/home/homeActivitiesDetails',
			data: {
				activitiesId: options.actId
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.actDetail = resp.data
				this.endTime = resp.data.endTime
				this.lat = resp.data.lat
				this.long = resp.data.lon
				this.venueName = resp.data.venueName
				this.address = resp.data.address
				this.organizerPhone = resp.data.phone
				this.clubId = resp.data.clubId
				const _endTime = (new Date(this.endTime.replace(/-/g,'/')).getTime()) / 1000
				this.countdowm(_endTime) //执行倒计时函数
			}
		})
	},
	computed: {
		showPeopleImg() {
			return this.list.length > 0
		}
	},
	methods: {
		toList() {
			
		},
		clickBanner(item) {
			console.log(item)
			if (this.inWhiteList) { //如果在小程序的网址白名单中，会走内置webview打开，否则会复制网址提示在外部浏览器打开
				uni.navigateTo({
					url: '/pages/out/out?url=' + item.webUrl
				});
			} else {
				uni.setClipboardData({
					data: item.webUrl
				});
				uni.showModal({
					content: '本网址无法直接在小程序内打开。已自动复制网址，请在手机浏览器里粘贴该网址',
					showCancel: false
				});
			}
		},
		// 地图导航
		toMap() {
			const that = this
			uni.openLocation({
					latitude: Number(that.lat),
					longitude:Number( that.long),
					name: that.venueName,
					address: that.address,
					scale: 13,
					success: function () {
							console.log('success');
					},
					fail: function () {
							console.log('fail');
					}
			});
		},
		// 拨打电话
		hanleCall() {
			uni.makePhoneCall({
			    phoneNumber: this.organizerPhone
			});
		},
		// 点击俱乐部
		toClub(clubId) {
			uni.navigateTo({
				url: '/pages/club/homePage/homePage?clubId=' + clubId
			})
		},
		checked() {
			this.isChecked = !this.isChecked
		},
		toEscapeClause() {
			
		},
		 // 倒计时
		countdowm(timestamp) {
			// console.log(timestamp)
			let self = this
			let timer = setInterval(function() {
				let nowTime = new Date();
				// let endTime = new Date(timestamp * 1000);
				let endTime = timestamp * 1000;
				// console.log(endTime)
				let t = endTime - nowTime.getTime();
				if(t > 0) {
					let day = Math.floor(t / 86400000)
					let hour = Math.floor((t / 3600000) % 24)
					let min = Math.floor((t / 60000) % 60);
					let sec = Math.floor((t / 1000) % 60);
					hour = hour < 10 ? "0" + hour : hour;
					min = min < 10 ? "0" + min : min;
					sec = sec < 10 ? "0" + sec : sec;
					let format = '';
					if(day > 0){
						format = `${day}天${hour}小时${min}分${sec}秒`;
					} 
					if(day <= 0 && hour > 0 ){
						format = `${hour}小时${min}分${sec}秒`; 
					}
					if(day <= 0 && hour <= 0){
						format =`${min}分${sec}秒`;
					}
					self.content = format;
					self.isTosignUp = true
				} else{
					self.isTosignUp = false
					clearInterval(timer);
					self.content = '报名结束';
					self._callback();
				}
			},1000)
		},
		_callback(){
			if(this.callback && this.callback instanceof Function){
				console.log(...this)
				this.callback(...this);
			}
		},
		submit() {
			// 判断是否登录
			if(uni.getStorageSync('isLogin')) {  //已登录
				// 判断是否加入俱乐部
				this.$http.get({
					url: '/v1/rest/home/isClubMember',
					data: {
						userId: uni.getStorageSync('userInfo').userId,
						clubId: this.clubId
					}
				}).then(resp => {
					console.log(resp)
					if(resp.status == 200) {
						if(resp.data.initClubMember) {  //已加入俱乐部
							if(this.isChecked) {  //已同意免责条款
								uni.navigateTo({
									url: '/pages/activity/pay/pay?actId=' + this.activityId
								})
							}else{  //未同意免责条款
								uni.showToast({
									title: '未同意《免责条款》！',
									duration: 2000,
									icon: 'none'
								});
							}
						}else{  //未加入俱乐部
							uni.showToast({
								title: '加入该俱乐部后方可报名！',
								duration: 2000,
								icon: 'none'
							});
							uni.navigateTo({
								url: '/pages/club/homePage/homePage?clubId=' + this.clubId
							})
						}
					}
				})
			}else{  //未登录
				uni.navigateTo({
					url: '/pages/login/login'
				})
			}
		}
	}
}
